import React from "react";

const ProgrammingCard = props => {
  return (
    <div>
        <h3 className="programName">{props["Program Name"]}</h3>
        <ul>
          <li><img  src={props.Photo} alt={props["Your Name"]} /><img  src={props.Photo2} /> </li>
          <li>Presenter: <a href={props["Link to speakers public profile (linked-in, Medium, or...)"]}>{props["Your Name"]}</a></li>
          <li>Description: {props["Longer Description"]}</li>
        </ul>
        <hr></hr>
    </div>
  );
};

export default ProgrammingCard;